@import "../scss/colors.scss";
@import "../scss/Typography.scss";
@import "../scss/buttons.scss";
@import "../../Assets/scss/breakpoints.scss";

$grid__bp-md: 1200;
html,
body {
  font-size: 100%;
  color: $color-primary;
  margin: 0;
  background-color: $color-background;
}

.container {
  max-width: 1200px;
  margin: auto;
  box-sizing: content-box !important;
  @media screen and (max-width: $width-sm) {
    margin: auto;
    max-width: 96vw;
  }
  @media screen and (max-width: $width-md) {
    margin: auto;
    max-width: 96vw;
  }
  @media screen and (max-width: $min-width-l) {
    margin: auto;
    max-width: 96vw;
  }
  @media screen and (max-width: $width-lg) {
    margin: auto;
    max-width: 96vw;
  }
  @media screen and (max-width: $width-xl) {
    margin: auto;
    max-width: 96vw;
  }
}
.padding-50 {
  padding-top: 50px;
  padding-bottom: 50px;
}
.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vertical-bottom {
  position: absolute;
  bottom: 0;
}

.blockquote-footer {
  font-family: $font-Blinker;
  font-size: 2em;
}

@media screen and (max-width: 599px) {
  .site-wrapper {
    margin: 1em;
  }
  .blockquote-footer {
    font-family: $font-Blinker;
    font-size: 1.3em;
  }

  p {
    font-family: $font-Blinker;
    font-size: 1.3em;
    line-height: 1.25em;
  }
}

.height-100 {
  min-height: 100vh;
}

.text-left {
  text-align: left;
}
@media screen and (max-width: 599px) {
  .tag-text {
    font-family: $font-Rubik;
    font-size: em;
    font-weight: 100;
  }
}

p {
  font-family: $font-Blinker;
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 1.3em;
}

h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.63em;
  margin-block-end: 0.63em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.mobile-only {
  display: none !important;
}
@media screen and (max-width: 599px) {
  .mobile-only {
    display: inline !important;
  }
  .desktop-only {
    display: none !important;
  }
}
.heading-text {
  font-family: $font-Blinker;
  font-size: 3.4rem !important;
  color: $color-primary;
  font-weight: 900;
  padding-bottom: 10px;
  position: relative;
  margin: auto;
}
.heading-text:before {
  content: "\2014 \00A0";
}

.heading-behind {
  width: 0rem;
  height: 6rem;
  text-transform: uppercase;
  font-family: $font-Blinker;
  font-size: 9em;
  font-weight: 700;
  @media screen and (min-width: $width-md) {
    font-size: 8em;
  }
}
//
.box-left-mini {
  width: 1px;
  height: 1px;
}

.box-left-mini .behind_container {
  color: $color-surface-variant;
  top: 18px;
}
.box-left-mini .behind {
  font-family: $font-Blinker;
  font-size: 17em;
  font-weight: 700;
}
//
.heading {
  font-size: 5em !important;
  color: $color-primary;
  font-weight: 700 !important;
  font-family: $font-Rubik;
  font-stretch: 500px;
  padding-bottom: 0.5em;
}
.heading:before {
  content: "\2014 \00A0";
}

.tagline-text {
  border-left: 0.2rem solid $color-primary;
  color: $color-secondary;
  position: relative;
  font-family: $font-Rubik;
  font-size: 2rem !important;
  line-height: 2.9rem !important;
  font-weight: 700 !important;
  padding: 0.1rem 0rem 0.1rem 0.5rem;
}

@media screen and (max-width: 599px) {
  .cover-heading {
    line-height: 0em;
  }

  .heading-behind {
    width: 0rem;
    // height: .3rem;

    font-size: 4em;
    font-weight: 700;
  }
  .heading {
    font-size: 2.5em !important;
    color: $color-primary;
    font-weight: 700 !important;
    font-family: $font-Rubik;
    font-stretch: 500px;
  }
}
