@import "../../Assets/scss/App.scss";
@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";

.about-section {
  background-color: $color-background-variant;
  .heading-behind {
    height: 7rem;

    color: $color-background;
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background-variant;
  }
}

.about-image {
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}
.about-text {
  font-family: $font-Blinker;
  font-size: 1.5rem !important;
  color: $color-on-background;
  line-height: 1.4em !important;
  padding-bottom: 10px;
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}
.about-focus {
  font-size: 1.5rem;
  padding: 10px 0;
}
@media screen and (max-width: 599px) {
  .about-section {
    .heading-behind {
      height: 2rem;
    }
  }
  .about-image {
    display: none;
  }
}

.custom-shape-divider-bottom-16070489245 {
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-bottom-16070489245 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 150px;
  transform: rotateY(180deg);
}

.custom-shape-divider-bottom-16070489245 .shape-fill {
  fill: $color-background-variant;
}

@media (max-width: 767px) {
  .custom-shape-divider-bottom-16070489245 svg {
    height: 91px;
  }
}
