@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/buttons.scss";
@import "../../Assets/scss/Padding.scss";

.education-section {
  background-color: $color-background-variant;
  .heading-behind {
    height: 7rem;
    color: $color-background;
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background-variant;
  }
}

.education-card {
  margin-right: 10px !important;
  padding: 10px 0px 0px 10px !important;
  background-color: $color-background;
  border-radius: 10px;
  border: 2px solid transparent;
  padding: 0.75em 0.1em 2.5em 0.1em;
  margin-bottom: 20px;
  margin-top: 20px;
}

.education-card:hover {
  border: 2px solid $color-secondary;
  transition: 2s ease;
}

.education-year {
  color: $color-secondary;
  font-size: 0.8rem !important;
  font-family: $font-Blinker;
}
.education-degree {
  color: $color-on-surface;
  font-family: $font-Blinker;
  font-size: 1.5rem !important;
  font-weight: 600 !important;
  text-align: left;
}
.education-school {
  color: $color-primary;
  font-size: 0.9rem !important;
  font-family: $font-Blinker;
}
.education-accomplishment {
  color: $color-on-background;
  font-family: $font-Blinker;
  font-size: 1rem !important;
}
@media screen and (max-width: 599px) {
  .education-section {
    .heading-behind {
      height: 2rem;
    }
  }

  .education-accomplishment {
    color: $color-primary;
    font-family: $font-Blinker;
    font-size: 1.1rem !important;
    line-height: 1.5rem !important;
  }
}
