@import "../../Assets/scss/App.scss";
@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/_breakpoints.scss";
@import "../../Assets/scss/buttons.scss";
.skills-section {
  background-color: $color-background;

  .heading-behind {
    height: 6rem;
    color: $color-background-variant;
    @media screen and (max-width: $width-sm) {
      height: 2rem;
    }
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background;
  }
}
.sub-heading {
  font-size: 2.2rem !important;
  font-weight: 500 !important;
  padding: 1em 0rem 0.5em 0em;
  font-family: $font-Rubik;
  color: $color-secondary;
}

// $host: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/397014/";

$card-width: 180px;
$card-width-sm: 30vw;
$card-width-md: 20vw;
$min-width-l: 10vw;
$card-width-lg: 180px;
$card-height: 160px;

$thumb-height: 120px;

$border-radius: 3px;
$box-shadow: 0 1px 4px rgba(black, 0.3);
$transition: cubic-bezier(0.17, 0.67, 0.5, 1.03);
$timing-1: 0.4s 0.15s;
$timing-2: 0.5s 0.25s;

@mixin size($width, $height) {
  width: $width;
  height: $height;
}

.skill-card {
  background-color: lighten($color-background-variant, 5%);
  text-align: center !important;
  @include size($card-width, $card-height);
  border-radius: $border-radius;
  box-shadow: $box-shadow;
  overflow: hidden;
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
  align-items: center !important;

  @media screen and (max-width: $width-sm) {
    margin-right: 7px;
    width: $card-width-sm;
  }

  @media screen and (min-width: $width-md) {
    width: $card-width-md;
  }
  @media screen and (min-width: $width-lg) {
    width: $card-width-lg;
  }
  .thumb {
    @include size(auto, $thumb-height);
    border-radius: $border-radius;
    img {
      background-repeat: no-repeat;
      @include size(auto, 100px);
      padding: 10px;

      @media screen and (max-width: $width-sm) {
        @include size(auto, 60px);
      }
    }
  }
  .infos {
    @include size(auto, $card-height);
    position: relative;
    padding: 4px 4px;
    background-color: lighten($color-background-variant, 10%);
    transition: $timing-1 $transition;

    .category {
      margin: 0.5em 0em;
      text-transform: uppercase;
      font-size: 0.75rem;
      color: $color-secondary;
      font-family: $font-Rubik;
    }
    .title {
      position: relative;
      color: $color-primary;
      font-family: $font-Rubik;
      font-size: 1.5rem;
      padding-top: 4px;
      padding-bottom: 5px;
      border-bottom: 3px solid lighten($color-background-variant, 5%);
      @media screen and (max-width: $width-sm) {
        font-size: 1rem;
      }
    }
    .image {
      background-repeat: no-repeat;
      @include size(auto, 50px);
      padding: 10px;
    }

    .detail {
      font-family: $font-Blinker;
      line-height: 1.5;
      font-size: 1.2rem;
      color: $color-on-surface;
      transition: $timing-2 $transition;
      font-weight: 300;
    }
  }
  .infos:hover {
    transform: translateY(-$thumb-height);
    .seats,
    .txt,
    .details {
      opacity: 1;
      display: inline !important;
    }
  }
  &:hover .infos {
    transform: translateY(-$thumb-height);

    .detail {
      opacity: 1;
    }
    .image {
      opacity: 1;
    }
    .title {
      position: relative;
      color: $color-primary;
      font-family: $font-Rubik;
      font-size: 1.5rem;
      transition: $timing-2 $transition;
      @media screen and (max-width: $width-sm) {
        font-size: 1rem;
      }
    }
  }
}
.skills-text {
  font-family: $font-Blinker;
  font-size: 1.6rem !important;
  color: $color-on-background;
  line-height: 1.4em !important;
  padding-bottom: 10px;
  display: block;
  width: 100%;
  position: relative;
  margin: auto;
}
