@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
.btn {
  text-transform: uppercase;
  border-radius: 10px;
  font-family: $font-Rubik;
  text-decoration: none;
  text-align: center;
  display: inline-block;
  margin-bottom: 10px;
}

.btn-small {
  font-weight: 500;
  font-size: 1.3rem;
  padding: 7px 15px;
  margin-right: 15px;
  margin-top: 15px;
}
.btn-medium {
  font-weight: 700;
  font-size: 2.5rem;
  padding: 9px 20px;
  margin-right: 20px;
}
.btn-large {
  font-weight: 900;
  font-size: 3.5rem;
  padding: 12px 25px;
  margin-right: 25px;
}

.btn-primary,
.btn-primary:focus {
  color: $color-background;
  background-color: $color-primary;
}
.btn-primary:hover {
  background-color: $color-secondary;
}

.btn-secondary,
.btn-secondary:focus {
  color: $color-on-background;
  background-color: $color-surface;
  margin-top: 13px !important;
  border: 2px solid $color-on-background;
  padding: 5px 15px !important;
}
.btn-secondary:hover {
  color: $color-on-surface;
  background-color: $color-primary;
  border: 2px solid $color-primary;
}

.btn-tertiary {
  font-weight: 500;
  color: inherit;
  text-decoration: underline dotted $color-on-background;
}
.btn-tertiary:hover {
  color: $color-on-background;
}

.btn-quaternary,
.btn-quaternary:focus .btn-quaternary:active {
  color: $color-primary;
  background-color: $color-background;
  border: 2px solid $color-background-variant;
  border-radius: 50% !important;
  width: 30px;
  height: 30px;

  padding: 0px;
}
.btn-quaternary:hover {
  color: $color-on-background;
  background-color: transparent;
  border: 2px solid $color-on-background;
  z-index: 999;
}

@media (prefers-color-scheme: light), (prefers-color-scheme: no-preference) {
  .btn-primary,
  .btn-primary:focus {
    color: $color-background;
    background-color: $color-primary;
    border: 2px solid $color-primary;
  }
  .btn-primary:hover {
    color: $color-primary;
    background-color: transparent;
    border: 2px solid $color-primary;
  }

  .btn-secondary,
  .btn-secondary:focus {
    color: $color-on-background;
    background-color: transparent;
    border: 2px solid $color-on-background;
  }
  .btn-secondary:hover {
    color: $color-on-background;
    background-color: inherit;
    border: 2px solid $color-on-background;
  }

  .btn-tertiary {
    font-weight: 500;
    color: inherit;
    text-decoration: underline dotted $color-on-background;
  }
  .btn-tertiary:hover {
    color: $color-on-background;
  }
}
