@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/buttons.scss";
@import "../../Assets/scss/Padding.scss";
@import "../../Assets/scss/breakpoints.scss";

.project-section {
  background-color: $color-background-variant;
  .heading-behind {
    height: 6rem;
    color: $color-background;
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background-variant;
  }
}

.projects-card {
  background-color: $color-surface-light;
  border-radius: 10px;
  border: transparent 2px solid;

  text-align: center;
  // padding: 0.75em 0.1em 2.5em 0.1em;
  // margin-bottom: 20px;
  // margin-top: 20px;

  .projects-name {
    color: $color-on-background;
    font-family: $font-Blinker;
    font-size: 0.5em !important;
    bottom: 0.5em;
    left: 5px;
    @media screen and (max-width: $width-sm) {
      font-size: 0.4em !important;
    }
  }
  .project-icon {
    max-width: 80px;
    height: 70px;
    margin: 0.6em 0.1em;
  }
}

.projects-card:hover {
  border: $color-secondary 2px solid;
  transition: 2s ease;
  @media screen and (max-width: $width-sm) {
    border: $color-secondary 2px solid;
    transition: 2s ease;
  }
  .project-icon {
    transform: scale(2);
    position: relative;
    @media screen and (max-width: $width-sm) {
      transform: scale(1.5);
    }
  }
  .project-icon {
    transition: 2s ease;
  }
}

$host: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/397014/";

$card-width: 380px;
$card-width-sm: 94vw;
$card-width-md: 45vw;
$card-width-lg: 380px;
$card-height: 350px;

$thumb-height: 280px;

$border-radius: 3px;
$box-shadow: 0 1px 4px rgba(black, 0.3);
$transition: cubic-bezier(0.17, 0.67, 0.5, 1.03);
$timing-1: 0.4s 0.15s;
$timing-2: 0.5s 0.25s;

@mixin pos($pos, $left: null, $top: null, $right: null, $bottom: null) {
  position: $pos;
  left: $left;
  @if $top {
    top: $top;
  }
  @if $left {
    left: $left;
  }
  @if $right {
    right: $right;
  }
  @if $bottom {
    bottom: $bottom;
  }
}

@mixin size($width, $height) {
  width: $width;
  height: $height;
}

.project-card {
  @include size($card-width, $card-height);
  border-radius: $border-radius;
  box-shadow: $box-shadow;
  overflow: hidden;
  display: inline-block;

  margin-right: 20px;
  margin-bottom: 20px;
  align-items: center !important;
  // background-color: green;

  @media screen and (max-width: $width-sm) {
    width: $card-width-sm;
    // background-color: yellow;
  }

  @media screen and (min-width: $width-md) {
    width: $card-width-md;
    // background-color: red ;
  }
  @media screen and (min-width: $width-lg) {
    width: $card-width-lg;
    // background-color: green;
  }
  .thumb {
    @include size(auto, $thumb-height);
    background-size: cover;
    border-radius: $border-radius;
  }
  .infos {
    @include size(auto, $card-height);
    position: relative;
    padding: 4px 24px;
    background-color: lighten($color-background-variant, 5%);
    transition: $timing-1 $transition;

    .category {
      margin: 0.5em 0em;
      text-transform: uppercase;
      font-size: 0.75rem;
      color: $color-secondary;
      font-family: $font-Rubik;
    }
    .title {
      position: relative;
      margin: 0.2em 0;
      color: $color-primary;
      font-family: $font-Rubik;
      font-size: 1.5rem;
      padding-bottom: 24px;
      border-bottom: 1px solid $color-background;
      transition: $timing-2 $transition;
      @media screen and (max-width: $width-md) {
        font-size: 1.3rem;
      }
    }
    .flag {
      @include pos(absolute, $right: 0);
      transform: translateY(-50%);
      @include size(35px, 23px);
      background-size: 100% auto;
      display: inline-block;
      opacity: 0;
      color: $color-primary !important;
      padding-top: 20px;
    }
    
    .description {
      font-family: $font-Blinker;
      line-height: 1.5;
      font-size: 1.5rem;
      color: $color-on-surface;
      // opacity:30;
      transition: $timing-2 $transition;
      font-weight: 300;
    }
    .txt {
      // font-family: $merriweather;
      line-height: 2;
      font-size: 0.95rem;
      color: $color-primary;
      opacity: 0;
      transition: $timing-2 $transition;
    }
    .details {
      position: absolute;
      bottom: 10px !important;
      margin: 10px 0;
      letter-spacing: 1px;
      color: $color-primary;
      font-size: 0.9rem;
      text-transform: uppercase;
      cursor: pointer;
      opacity: 0;
      transition: $timing-2 $transition;
      a[href] {
        position: relative;
        &,
        &:link,
        &:visited,
        &:active {
          text-decoration: none;
          padding-bottom: 3px;
          font-weight: bold;
        }
        &::after {
          content: "";
          background: $color-primary;
          @include size(0, 1px);
          transition: 0.35s $transition;
        }
        &:hover::after {
          width: 100%;
          box-shadow: $box-shadow;
        }
      }
    }
  }
  .infos:hover {
    transform: translateY(-$thumb-height);
    
    .details {
      opacity: 1;
    }
  }
  &:hover .infos {
    transform: translateY(-$thumb-height);
   
    .details {
      opacity: 1;
    }
    .flag {
      opacity: 1;
      transition: $timing-2 $transition;
    }
  }
}