@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/buttons.scss";
@import "../../Assets/scss/Padding.scss";
@import "../../Assets/scss/_breakpoints.scss";


.relative {
  position: relative;
  background-color: inherit;
}

.heading-behind-cover {
  width: 0rem;
  height: 7rem;

  color: $color-surface-variant-light;

  text-transform: uppercase;
  font-family: $font-Blinker;
  font-size: 7em;
  font-weight: 700;
}

.coverheader {
  background-color: red !important;
  clip-path: polygon(100% 0%, 100% 90%, 69% 100%, 0% 85%, 0% 0%);
  background-attachment: fixed;
}
.mask {
  mask-type: luminance; /* white = transparent, grays = semi-transparent, black = opaque */
  mask-type: alpha; /* transparent areas of the image let image through, otherwise not */
}

.cover-height {
  min-height: 87vh;
}
.cover-section {
  background-color: $color-background;
}

.cover-name {
  font-family: $font-Rubik;
  font-size: 4em !important;
  font-weight: 600 !important;
  height: 0.8em;
  padding-bottom: 0.4em;
  @media screen and (max-width: $width-md) {
    font-size: 3em !important;
  }

}
.cover-title {
  font-family: $font-Blinker;
  color: $color-on-background;
  font-size: 4em !important;
  height: 1.2em;
  @media screen and (max-width: $width-md) {
    font-size: 3em !important;
    // height: 0.8em;
  }
}
.cover-title-list {
  font-family: $font-Blinker;
  font-size: 1.5em !important;
  padding-bottom: 0.4em;
}
.tagline-text-cover {
  border-left: 0.2rem solid $color-primary;
  color: $color-secondary;
  position: relative;
  font-family: $font-Rubik;
  font-size: 3rem !important;
  line-height: 3rem !important;
  font-weight: 500 !important;
  padding: 0.5rem 0rem 0.5rem 0.5rem;
}

.cover-text {
  font-size: 2rem;
  line-height: 1.5em;
  font-weight: 500;
  text-align: left;
}
.cover-image {
  display: inline-block;
  width: 100%;
  margin: auto;
  border-radius: 30%;
}
.cover-image-mobile {
  display: none;
}

@media screen and (max-width: 599px) {
  .cover-height {
    min-height: 77vh;
  }

  .cover-name {
    font-size: 2.2em !important;
    padding-top: 0.05em;
  }
  .cover-title {
    font-size: 2rem !important;
    padding-bottom: 0.1rem;
  }
  .cover-title-list {
    font-size: 1.1em !important;
    padding-bottom: 1.4em;
  }
  .tagline-text-cover {
    font-size: 1.7rem !important;
    line-height: 2.2rem !important;
    font-weight: 400 !important;
  }
  .cover-image-mobile {
    text-align: center;
    padding-top: 1.8rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
    img {
      height: 280px;
    }
  }
  .cover-image {
    display: none;
  }
}

@media (prefers-color-scheme: light) {
  .cover-section {
    background-color: $color-background-light;
  }
  .cover-heading {
    color: $color-surface-variant-light-light;
  }
  .cover-title {
    color: $color-on-background-light;
    font-size: 1.5rem !important;
  }
  .tagline-text-cover {
    border-left: 0.2rem solid $color-primary-light;
    color: $color-secondary-light;
  }
}

.custom-shape-divider-bottom-1607047764 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.custom-shape-divider-bottom-1607047764 svg {
  position: relative;
  display: block;
  width: calc(108% + 1.3px);
  height: 255px;
  transform: rotateY(180deg);
}

.custom-shape-divider-bottom-1607047764 .shape-fill {
  fill: #0fdf57;
}

@media (max-width: 767px) {
  .custom-shape-divider-bottom-1607047764 svg {
    width: calc(119% + 1.3px);
    height: 251px;
  }
}

.cover-section {
  .heading-behind {
    height: 7rem;
  }
  .custom-shape-divider-bottom-16070489245 .shape-fill {
    fill: $color-background;
  }
}
