@import "../../Assets/scss/Colors.scss";
@import "../../Assets/scss/Typography.scss";
@import "../../Assets/scss/breakpoints.scss";

.footer-section {
  background-color: $color-background;
  padding: 0px 0px 20px 0px;
}

footer {
  color: $color-primary;
  text-align: center;
  padding-top: 50px;
  padding-bottom: 30px;
  img {
    height: 35px;
    width: auto;
    display: inline;
    @media screen and (max-width: $width-sm) {
      height: 2.5em;
      padding-bottom: 10px;
    }
  }
  .force-left {
    text-align: left;
    @media screen and (max-width: $width-sm) {
      align-items: flex-end !important;
      text-align: inherit;
    }
  }
  .force-right {
    text-align: right;
    @media screen and (max-width: $width-sm) {
      align-items: flex-end !important;
      text-align: inherit;
    }
  }
  .btn-footer-link {
    font-family: $font-Rubik;
    padding: 0px 7px;
    color: $color-primary;
    font-size: 1.2em;
    text-decoration: none;
    @media screen and (max-width: $width-sm) {
      padding: 0px 10px;
      font-size: 0.9em;
    }
  }

  .footer-icon {
    color: $color-on-background;
    font-size: 1.8rem;
    padding: 0.5em 0;
    @media screen and (max-width: $width-sm) {
    }
  }

  .footer-text {
    color: $color-on-background;
    font-family: $font-Blinker;
    font-size: 1.3rem;
    @media screen and (max-width: $width-sm) {
      font-size: 1.2rem;
    }
  }

  @media screen and (max-width: $width-sm) {
    .desktop-footer {
      display: none;
    }
  }
}

.custom-shape-divider-bottom-1607046190 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);

  svg {
    position: relative;
    display: block;
    width: calc(108% + 1.3px);
    height: 90px;
    transform: rotateY(180deg);
  }
  .shape-fill {
    fill: $color-background;
  }
}
