// -- Dark
// https://material.io/design/color/dark-theme.html#ui-application

$color-primary: rgb(41, 199, 172);
$color-primary-variant: rgb(0, 64, 128);
$color-secondary: rgb(236, 98, 95);
$color-secondary-light: rgba(236, 97, 95, 0.091);
$color-secondary-variant: rgb(64, 128, 0);
$color-background: #19181a;
$color-background-variant: lighten($color-background, 3%);
$color-surface: rgb(31, 36, 39);
$color-surface-light: rgb(31, 36, 39);
$color-surface-variant: rgb(31, 36, 39);
$color-surface-variant-light: rgba(31, 36, 39, 0.392);
$color-error: rgb(18, 18, 18);
$color-on-primary: rgb(0, 0, 0);
$color-on-secondary: rgb(0, 0, 0);
$color-on-background: rgb(217, 217, 217);
$color-on-surface: rgb(255, 255, 255);
$color-on-error: rgb(0, 0, 0);

